$(window).on('load', () => {
  // const spinner = document.getElementById("js-loading");
  const spinner = $('#js-loading');
  const title = $('.p-slide-in');
  const titleInner = $('.p-slide-in__inner');
  const wind = $('.p-hero__icon-wind');
  const leaf = $('.p-hero__icon-leaf');

  $.when($(spinner).addClass('loaded'))
    .done(() => {
      $(title).addClass('slideLeftRight');
      $(titleInner).addClass('slideRightLeft');
    })
    .done(() => {
      $(wind).html(' <img class="p-hero__icon wind" src="./images/hero-flow.png" alt="top-icon" width="300" height="180" />');
    })
    .done(() => {
      $(leaf).html(' <img class="p-hero__icon leaf" src="./images/leaf.png" alt="top-icon" width="300" height="180" />');
    })
    .fail(() => {
      alert('読み込みに失敗しました。再読み込みを実行してください');
    });
});
